import axios from "axios";
import { NextResponse } from "next/server";

export async function GET() {
  try {
    const response = await axios.get(
      "https://tools.quicko.com/api/tds-rates/individual",
      {
        headers: {
          "User-Agent": "Mozilla/5.0",
          "Accept": "application/json"
        },
        timeout: 10000
      }
    );

    return NextResponse.json({
      success: true,
      data: response.data
    });
  } catch (error) {
    console.error("Quicko API Error:", error.message);

    return NextResponse.json(
      {
        success: false,
        message: "Unable to fetch live TDS rates"
      },
      { status: 500 }
    );
  }
}
